import React, { useState } from "react";
import { Link, useParams, useNavigate } from "react-router-dom";
import UserServices from "../services/usernameServices";
import Swal from "sweetalert2";
import AuthServices from '../services/authServices';
import './PasswordUpdate.css'
import ModalLoadingContacto from "./ModalLoadingContacto";

const PasswordUpdate = () => {
    const navigate = useNavigate();
    const { idUser } = useParams();

    const initialPasswordState = {
        id: idUser,
        currentPassword: "",
        newPassword: ""
    }

    const [password, setPassword] = useState(initialPasswordState);
    const [validPassword, setValidPassword] = useState({});
    const [showModal, setShowModal] = useState(false);

    const handleInputChange = event => {
        const { name, value } = event.target;
        setPassword({ ...password, [name]: value });
    };

    const handleInputblurPassword = (event) => {
        handleInputChange(event);
        setValidPassword(validationPassword(password));

    };

    const showModalHandler = () => {
        setShowModal(true);
    };

    const closeModalHandler = () => {
        setShowModal(false);
    };

    const validationPassword = (password) => {
        let validPassword = {}

        if (
            password.newPassword.length < 8 ||
            !/[A-Z]/.test(password.newPassword) ||
            !/[0-9]/.test(password.newPassword) ||
            !/[!@#$%^&*]/.test(password.newPassword)
        ) {
            validPassword.password =
                "La contrasea debe tener al menos 8 caracteres, una letra mayscula, un nmero y un carcter especial";
        }

        return validPassword;
    }

    const passwordUpdate = (e) => {
        e.preventDefault();
        showModalHandler();
        const token = AuthServices.getAuthToken();
        if (token) {
            UserServices.setAuthToken(token);
        } else {
            console.error("No se encontr un token vlido");
            closeModalHandler();
            return;
        }
        setValidPassword(validationPassword(password));
        if (Object.keys(validPassword).length === 0) {
            UserServices.updatePassword(password)
                .then(response => {
                    console.log(response.data);
                    closeModalHandler();
                    Swal.fire({
                        position: 'top-center',
                        icon: 'success',
                        title: 'contrasea Actualizada Correctamente',
                        showConfirmButton: false,
                        timer: 2000
                    }).then(
                        navigate("/")
                    )
                })
                .catch(e => {
                    console.log(e);
                    closeModalHandler();
                    Swal.fire('Digite la actual contrasea correcta')
                });
        } else {
            closeModalHandler();
        }

    };

    return (
        <div className="password-container">
            <div className="card">
                <div className="card-body ">
                    <div className="content">
                        <h3> <i className="bi bi-exclamation-triangle"> Cambiar Contrasea</i></h3>
                        <hr className="dropdown-divider" />
                        <form onSubmit={passwordUpdate}>
                            <div className="mb-3">
                                <label className="form-label">Contrasea Actual</label>
                                <div className="input-group">
                                    <span className="input-group-text">
                                        <i className="bi bi-key"></i>
                                    </span>
                                    <input
                                        type="password"
                                        className="form-control"
                                        placeholder="Contrasea Actual"
                                        id="currentPassword"
                                        name="currentPassword"
                                        value={password.currentPassword}
                                        onChange={handleInputChange}
                                        required
                                        style={{ width: '300px' }}
                                    />
                                </div>
                            </div>
                            <div className="mb-3">
                                <label className="form-label">Nueva contrasea</label>
                                <div className="input-group has-validation">
                                    <span className="input-group-text">
                                        <i className="bi bi-lock"></i>
                                    </span>
                                    <input
                                        type="password"
                                        className={((validPassword.password) ? "is-invalid" : "") + " form-control"}
                                        id="newPassword"
                                        name="newPassword"
                                        placeholder="Ejemplo1*"
                                        value={password.newPassword}
                                        onBlur={handleInputblurPassword}
                                        onChange={handleInputChange}
                                        onKeyUp={handleInputblurPassword}
                                        style={{ maxWidth: '300px' }}
                                        required
                                    />
                                </div>
                                {validPassword.password &&
                                    <div className="container mb-3" style={{ maxWidth: '300px' }}>
                                        <label className="form-label errorSmall">
                                        <i className="bi bi-exclamation-circle"> {validPassword.password} </i></label>
                                    </div>
                                }
                            </div>


                            <div className="col-12">

                                <button className="btn btn-secondary my-3 mx-2" type="submit">
                                    <i className="bi bi-gear"></i> Actualizar
                                </button>
                                <Link className="btn btn-danger" to={"/"}>
                                    <i className="bi bi-x-circle"></i> Cancelar
                                </Link>
                            </div>
                        </form>

                        {showModal && (
                            <ModalLoadingContacto />
                        )}
                    </div>
                </div>
            </div >
        </div >



    );
};

export default PasswordUpdate;