import React from "react";
import rentLogo from '../assets/img/rentLogo.png'
import prado from '../assets/img/prado.jpg';
import rush from '../assets/img/rush.jpg';
import hilux from '../assets/img/hilux.jpg';
import jimy from '../assets/img/jimny.jpg';
import './IndexUNA.css';
import { Link} from "react-router-dom";
import ContactoFooter from './ContactoFooter';
const IndexUNA = () => {
 
    return (

        <div className="index">
            <h1 className="title">Go-Tico Car Rentals</h1>
            <div className="containerCarousel">
                <div >
                    <div id="carouselExampleControls" className="carousel slide" data-bs-ride="carousel" >
                        <div className="carousel-inner tamano">

                            <div className="carousel-item active ">
                                <img src={rentLogo} className="d-block w-100 imagen-carousel" alt="..." />
                            </div>
                          
                            <div className="carousel-item  ">
                                <img src={prado} className= "d-block w-100 imagen-carousel" alt="..." />
                            </div>

                            <div className="carousel-item">
                                <img src={jimy} className="d-block w-100 imagen-carousel" alt="..." />
                            </div>

                            <div className="carousel-item">
                                <img src={hilux}className="d-block w-100 imagen-carousel" alt="..." />
                            </div>

                            <div className="carousel-item">
                                <img src={rush} className="d-block w-100 imagen-carousel" alt="..." />
                            </div>
                        </div>

                        <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
                            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Previous</span>
                        </button>
                        <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
                            <span className="carousel-control-next-icon" aria-hidden="true"></span>
                            <span className="visually-hidden">Next</span>
                        </button>

                    </div>
                </div>
            </div>
            <div className="info" >
                <div className="container">

                    <h2 className="h2-style">
                        La mejor opcin para alquilar
                        <br />
                        un vehculo es con nosotros

                    </h2>

                    <div className=" d-flex flex-wrap" >
                        <p style={{ color: "white" }}>
                            "Descubre la libertad y la comodidad con nuestros excepcionales automviles de alquiler!<br />
                            Nuestros vehculos ofrecen una combinacin perfecta de estilos, rendimientos y practicidad.<br />
                            Con diseos elegantes y aerodinmicos, estos vehiculos llaman la atencin por dondequiera que vayan.<br />
                            Confa en nosotros para satisfacer tus necesidades de transporte.<br />
                            Ya sea un viaje de negocios o unas vacaciones en familia, nuestros automviles de alquiler estan listos<br />
                            para hacer que tu experiencia sea inolvidable. Descubre la verdadera libertad de movimiento y reserva ahora<br />
                            para disfrutar de un viaje lleno de confort y emocin!"
                        </p>
                    </div>
                    <div className="d-flex flex-wrap ">
                        <Link className="btn btn-info" to={"/IndexRent"}
                            style={{ color: 'white', fontFamily: 'Arial, sans-serif' }}>
                            Ver Vehculos
                        </Link>
                        <span style={{ marginRight: '10px' }}></span>

                        <Link className="btn btn-danger" to={"/UserCreate"}
                            style={{ color: 'white', fontFamily: 'Arial, sans-serif' }}>
                            Regstrese
                        </Link>
                    </div>
                </div>
            </div>
            <div className="continfobeneficios">
                <div className="container">

                    <h2 className="rc-section-title">Por qu alquilar con nosotros?</h2>
                    <div className="row">
                        <div className="col-lg-3 col-md-6">
                            <div className="benefit-item">
                                    <i className="bi bi-cash-coin incoSize" style={{color : "#82CD54"}}></i>
                                    <h4>El mejor precio</h4>
                                <p className="text-justify">
                                    Constantemente presentamos ofertas especiales y descuentos promocionales. Algunas promociones como descuentos por reserva anticipada, tarifas especiales para periodos prolongados o paquetes.
                                </p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="benefit-item">
                                <i className="bi bi-collection incoSize" style={{color : "#F07DCB"}}></i>
                                <h4>Amplia seleccin</h4>
                                <p className="text-justify">
                                    Haz tu reserva con nosotros y te brindamos la posibilidad de elegir el vehculo que mejor se adapte a tus
                                    necesidades y preferencias. Puedes encontrar desde autos compactos hasta camionetas o vehculos de lujo.
                                </p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="benefit-item">
                                <i className="bi bi-card-checklist incoSize" style={{color: "#DAC2AA"}}></i>
                                <h4>Servicios adicionales</h4>
                                <p className="text-justify">
                                    Te ofrecemos servicios adicionales que otras compaas no, como asistencia en carretera, GPS integrado,
                                    sillas para nios, conductor adicional, entre otros. Estos servicios pueden hacer tu viaje ms seguro,
                                    cmodo y adaptado a tus necesidades especficas.
                                </p>
                            </div>
                        </div>
                        <div className="col-lg-3 col-md-6">
                            <div className="benefit-item">
                                <i className="bi bi-car-front-fill incoSize" style={{color: "#9FCDEA"}}></i>
                                <h4>Comodidad y privacidad</h4>
                                <p className="text-justify">
                                    Con nosotros, puedes disfrutar de mayor comodidad y privacidad durante tus desplazamientos. Adems de la
                                    rapidez y comodidad del alquiler en Go-Tico Car Rentals, tambin puedes contar con la asistencia los 7 das
                                    de la semana de un equipo de servicio especializado listo para ayudarte cuando lo necesites.
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <ContactoFooter />
        </div>
        

    )
}

export default IndexUNA;
